@import "../../common/colors.scss";

.header{
    background-color: $secondary-color;
    height: 72px;
    padding: 0px 42px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.logo{
    color: $font-primary;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
}

.user-image, .user-image img {
    width: 38px;
    height: 38px;
}
.user-image img{
    border-radius: 20px;
}

.search-bar {
    width: 50%;
    display: flex;
    justify-content: center;
    
    form {
        display: flex;
        justify-content: center;
        width: 70%;
       
        button {
            padding: 0px 8px;
            font-size: 20px;
            cursor: pointer;
            width: 60px;
            border-radius: 4px;
            margin-left: 3px;
        }
        input{
            border-radius: 4px;
            width: 400px;
            min-width: 100px;
            outline: none;
            font-size: 15px;
            padding: 5px;
        }
    }
}