@import "../../common/colors.scss";

.card-item {
    background: $secondary-color;
    cursor: pointer;
    transition: all 0.3s;
    min-height: 500px;
    height: 100%;
    margin: 10px;
    border-radius: 12px;
    

    &:hover{
        transform: scale(1.05);
        transition: all 0.3s;
    }
}

.card-top {
    height: 300px;
}       

img{
    width: 100%;
    height: 100%;
    border-radius: 12px;
}
.card-info {
    color: $font-primary;
    h4{
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 10px;
    }
}
.card-bottom {
    padding: 20px;
}